export default{
	name:"song-item",
	props: ["song"],
	onLoad(){
		console.log(this.song)
	},
	methods:{
		singerFilter(array){
			if(!array) return "---"
			// console.log(1,array)
			array = array.map(item=>{
				return item.name
			})
			return array.join("/")
		},
		goToPlay(){
			console.log("play")
			uni.navigateTo({
				url:"../play/play"
			})
		}
	}
}
